
import { defineComponent } from "vue";
import { useRouter } from "vue-router";

export default defineComponent({
    name: "FourZeroFour",
    setup() {
        const router = useRouter();

        const back = (index: number) => {
            switch (index) {
                case 0:
                    router.push("/dashboard");
                    break;
                case 1:
                    router.back();
                    break;
            }
        };

        return {
            back,
        };
    },
});
